<template>
  <v-container>
    <v-row>
        <v-col cols="12">
          <h1 class="display-2 font-weight-bold mb-3">CoEd</h1>
        </v-col>
    </v-row>

    <v-row>
      <v-col cols="12" md="3" lg="2">
        <v-card>
          <v-card-text style="text-align: center">
            <div>Record</div>
            <div class="text-h4 text--primary pt-6">{{wins}} - {{losses}}</div>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Home Runs</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{insidehrs}}</td>
                  <td>Inside the Park</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{outsidehrs}}</td>
                  <td>Over the Fence</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{totalhrs}}</td>
                  <td>Total Home Runs</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col cols="6" md="3" lg="2">
        <v-card>
          <v-card-text>
            <div>Run Differential</div>
            <v-simple-table dense>
              <tbody>
                <tr>
                  <td style="text-align: right">{{runsfor}}</td>
                  <td>Runs For</td>
                </tr>
                <tr>
                  <td style="text-align: right">{{runsagainst}}</td>
                  <td>Runs Against</td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row>
      <v-col cols="12" lg="8">
        <v-data-table
          :headers="headers"
          :items="softballgames"
          :sort-by="['gamedate', 'gametime']"
          :sort-desc="[false, false]"
          multi-sort
          disable-pagination
          hide-default-footer
          class="elevation-1"
        ></v-data-table>
      </v-col>
    </v-row>

    <v-row>
      <v-col>
      <v-btn
        color="primary"
        to="/"
      >
        <v-icon
          dark
          left
        >
          mdi-home
        </v-icon>Home
      </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import json from "@/assets/coed-schedule.json";

export default {
  data() {
    return {
      headers: [
        /*{ text: "Order", value: "order", align: "center" },*/
        /*{ text: "Type", value: "type" },*/
        { text: "Game Date", value: "gamedate"},
        { text: "Opponent", value: "opponent" },
        { text: "Game Time", value: "gametime", align: "center" },
        { text: "Field", value: "field", align: "center" },
        { text: "Result", value: "result", align: "center"},
        { text: "Our Score", value: "ourscore", align: "right" },
        { text: "Their Score", value: "theirscore", align: "right" },
      ],
      softballgames: [],
      wins: 0,
      losses: 0,
      runsfor: 0,
      runsagainst: 0,
      insidehrs: 0,
      outsidehrs: 0,
      totalhrs: 0
    };
  },
  mounted(){
      this.loadData();
  },
  methods: {
    loadData() {
      this.softballgames = json.softballgames;

      for (let i = 0; i < json.softballgames.length; i++) 
      {
        if(json.softballgames[i].result == "W") this.wins++;
        if(json.softballgames[i].result == "L") this.losses++;
        if(json.softballgames[i].ourscore != "") this.runsfor += parseInt(json.softballgames[i].ourscore);
        if(json.softballgames[i].theirscore != "") this.runsagainst += parseInt(json.softballgames[i].theirscore);
        if(json.softballgames[i].insidehrs != "") 
        {
          this.insidehrs += parseInt(json.softballgames[i].insidehrs);
          this.totalhrs += parseInt(json.softballgames[i].insidehrs);
        }
        
        if(json.softballgames[i].outsidehrs != "") 
        {
          this.outsidehrs += parseInt(json.softballgames[i].outsidehrs);
          this.totalhrs += parseInt(json.softballgames[i].outsidehrs);
        }
      }
    },
  },
};
</script>